<template>
    <main id="pay">
        <div class="row" style="margin-">
            <div class="col-md-2 product" v-for="paymentObject in paymentObjectArray" :key="paymentObject.id">
                <div class="alert alert-success product">
                    <h3>{{paymentObject.name}}</h3>{{paymentObject.description}}
                    <h2 style="color:red;">￥{{paymentObject.price/100}}</h2>
                    <button class="btn btn-warning" @click="addOrderGetToken(paymentObject.price);">购买</button>
                </div>
            </div>
        </div>
        <div class="row" style="text-align: center;padding-top:30.5%;">
            <div class="col">
                <hr>
                <img src="../../assets/images/common/yinlianLOGO.png" style="height:45px;" oncontextmenu="return false"
                    onselectstart="return false" ondragstart="return false"><br>
                <h3>目前仅支持银联支付</h3>
            </div>
        </div>

        <form id="payForm" action="http://localhost:8760/pay/pay/payIndex.action" method="get">
            <input name="token" type="hidden" v-model="payToken">
        </form>
    </main>
</template>

<script>
    export default {
        name: 'pay',
        data() {
            return {
                paymentObject: {
                    id: "",
                    name: "",
                    price: "",
                    description: ""
                },
                paymentObjectArray: [],
                payToken: "",
                vip_time: "0"
            }
        },
        methods: {
            async addOrderGetToken(price) { //添加订单并返回一个token,有限时
                var flag = true; //标记是否登录
                await this.axios({ //同步提交,避免表单元素的值未修改就提交表单
                    method: "post",
                    url: "/mobile/pay/createVipOrder.action",
                    data: price.toString()
                }).then((response) => {
                    if (response.data == "202") { //用户登录信息过期
                        flag = false;
                    } else { //返回订单token
                        this.payToken = response.data;
                    }
                }).catch((error) => {
                    console.log(error);
                });
                if (flag == true) {
                    $("#payForm").submit();
                } else {
                    this.$emit("showAlert", "true", "请先登录");
                }
            },
            toPay(token) { //根据订单token去支付页面
                this.axios({
                    url: "/pay/pay/payIndex.action",
                    method: 'get',
                    params: {
                        "token": token
                    }
                }).then((response) => {
                    // this.$router.push('/pay');
                    // alert("支付成功!");
                }).catch((error) => {
                    console.log(error);
                });
            },
            getVipItem() {
                this.axios({
                    method: "get",
                    url: "/mobile/pay/getAllVipItem.action"
                }).then((response) => {
                    this.paymentObjectArray = response.data;
                }).catch((error) => {
                    console.log(error);
                });
            },
        },
        mounted() {
            this.getVipItem();
        }
    }
</script>

<style scoped>
    .product {
        margin: 5px;
        text-align: center;
    }
</style>