<template>
    <main id="user">
        <div class="row">
            <div class="col">
                <div class="alert alert-primary" role="alert">
                    <span style="font-size:35px;">个人信息</span><br>

                    <table class="table table-hover">
                        <thead>
                            <tr>
                                <td>
                                    <img src="../../assets/images/common/favicon.png" class="img-thumbnail">
                                </td>
                                <td>
                                    <br>
                                    <button class="btn btn-light glyphicon glyphicon-user"> 更改个人头像</button>
                                    <br>
                                    <button class="btn btn-info glyphicon glyphicon-tasks" @click="modifyUser">
                                        {{modifyOrSave}}</button>
                                    <br><br>
                                    <button class="btn btn-primary glyphicon glyphicon-lock"> 修改密码</button>
                                </td>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>
                                    <span style="font-size:20px;">姓名</span>
                                </td>
                                <td>
                                    <span style="font-size:20px;">
                                        <span v-if="!ifModify">{{user.name}}</span>
                                        <input v-if="ifModify" v-model="user.name" type="text" class="form form-control"
                                            style="height:100%;font-size:10px;">
                                    </span>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <span style="font-size:20px;">邮箱</span>
                                </td>
                                <td>
                                    <span style="font-size:20px;">
                                        <span v-if="!ifModify">{{user.email}}</span>
                                        <input v-if="ifModify" v-model="user.email" type="text"
                                            class="form form-control" style="height:100%;font-size:10px;">
                                    </span>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <span style="font-size:20px;">会员剩余</span>
                                </td>
                                <td>
                                    <span style="font-size:20px;">{{vip_time}}
                                        <a class="glyphicon glyphicon-yen btn btn-warning" href="/#/pay"
                                            style="margin-bottom:5px;margin-left:15px;">续费</a>
                                    </span><br>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
            <div class="col">
                <div class="alert alert-warning" role="alert">
                    <span style="font-size:35px;">订阅信息</span><br>
                    <table class="table table-hover" style="text-align: center;">
                        <thead>
                            <tr>
                                <td>
                                    <span style="font-size:20px;">地区</span><br>
                                </td>
                                <td>
                                    <span style="font-size:20px;">管理</span>
                                    <button style="margin-left:25px;margin-bottom:6px;"
                                        class="btn btn-success glyphicon glyphicon-plus"
                                        @click="addSubscription"/>
                                    <br>
                                </td>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-if="subscriptionList.length==0">
                                <td colspan="2">
                                    <h1 style="margin-top:23px;">暂无订阅信息</h1>
                                </td>
                            </tr>
                            <tr v-for="subscription in subscriptionList" :key="subscription.id">
                                <td>
                                    <span style="font-size:20px;">{{subscription.provinceZh}} - {{subscription.cityZh}}</span><br>
                                </td>
                                <td>
                                    <div class="btn-group" role="group" aria-label="Basic example">
                                        <button class="btn btn-info glyphicon glyphicon-map-marker"
                                            @click="$emit('getLocation',subscription.provinceZh,subscription.cityZh)">快速定位</button>
                                        <button class="btn btn-danger glyphicon glyphicon-remove"
                                            @click="deleteSubscription(subscription.id)">移除该地区</button>
                                    </div>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>

        <div class="row">
            <div class="col">
                <cloud></cloud>
            </div>
        </div>
    </main>
</template>

<script>
    import dateUtil from '../../assets/js/date.js';

    import cloud from '../../components/pc/CloudFlow.vue';

    export default {
        name: 'user',
        data() {
            return {
                user: {
                    id: '',
                    name: '请登录',
                    email: '请登录',
                    vip_start: '请登录',
                    vip_stop: '请登录'
                },
                vip_time: '请登录',
                subscription: {
                    id: '',
                    uId: '',
                    provinceZh: '',
                    cityId: '',
                    cityZh: '',
                    ifEmail: ''
                },
                subscriptionList: [],
                ifModify: false,
                modifyOrSave: '修改个人信息'
            }
        },
        components: {
            cloud
        },
        methods: {
            getUser() {
                this.axios({
                    url: '/mobile/user/getUserMessage.action',
                    method: 'get'
                }).then((response) => {
                    if (response.data != "" && response.data != null) {
                        this.user = response.data;
                        var vip_stop = response.data.vip_stop;
                        if (vip_stop == null) {
                            this.vip_time = "未开通会员或已到期";
                        } else {
                            var vipTime = dateUtil.getTimeSpace((new Date()).getTime(), vip_stop);
                            if (vipTime[0] > 0) {
                                this.vip_time = "剩余" + vipTime[0].toFixed(0) + "天";
                            } else {
                                this.vip_time = "未开通会员或已到期";
                            }
                        }
                    } else {
                        this.user.name = '请登录';
                        this.user.email = '请登录';
                        this.user.vip_stop = '请登录';
                        this.vip_time = '请登录';
                    }
                }).catch((error) => {
                    console.log(error);
                });
            },
            modifyUser() {
                if (this.user.email == "请登录") { //是否登录判断
                    this.$emit("showAlert", true, "未登录");
                } else {
                    if (this.ifModify == true) {
                        if (!(/^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/).test(this.user
                                .email)) { //前端判断邮箱格式
                            this.$emit("showAlert", true, "请填写正确邮箱");
                        } else {
                            this.axios({
                                url: '/mobile/user/updateEmailAndName.action',
                                method: 'post',
                                data: this.user
                            }).then((response) => {
                                if (response.data == 1) { //修改成功
                                    this.modifyOrSave = "修改个人信息";
                                    this.ifModify = false;
                                    this.$emit("showAlert", true, "修改成功");
                                    this.$emit("getUser");
                                    this.getUser();
                                } else if (response.data == -1) { //未登录
                                    this.$emit("showAlert", true, "未登录");
                                } else if (response.data == 0) { //邮箱格式错误
                                    this.$emit("showAlert", true, "请填写正确邮箱");
                                } else if (response.data == 2) { //服务器异常
                                    this.$emit("showAlert", true, "服务器无响应,请稍后重试");
                                }
                            }).catch((error) => {
                                console.log(error);
                            });
                        }
                    } else {
                        this.ifModify = true;
                        this.modifyOrSave = "保存";
                    }
                }
            },
            getSubscription() {
                this.axios({
                    url: '/mobile/user/getSubscription.action',
                    method: 'get'
                }).then((response) => {
                    if (response.data == null) {
                        this.$emit("showAlert", true, "请登录");
                    } else {
                        this.subscriptionList = response.data;
                    }
                }).catch((error) => {
                    console.log(error);
                });
            },
            addSubscription() {
                var confi = confirm("订阅当前选中的城市?");
                if (confi == true) {
                    this.axios({
                        url: '/mobile/user/addSubscription.action',
                        method: 'get'
                    }).then((response) => {
                        if (response.data == 1) { //订阅成功
                            this.getSubscription();
                            this.$emit("showAlert", true, "订阅成功");
                        } else if (response.data == 0) { //未登录
                            this.$emit("showAlert", true, "请先登录,若已登录请稍后重试");
                        } else if (response.data == -1) { //重复订阅
                            this.$emit("showAlert", true, "订阅失败,已经订阅该地区");
                        } else if (response.data == 2) { //非会员订阅失败
                            this.$emit("showAlert", true, "订阅失败,当前订阅个数已达到上限,开通会员可以提升上限");
                        } else if (response.data == 3) { //会员订阅失败
                            this.$emit("showAlert", true, "抱歉!订阅失败,当前订阅个数已达到上限");
                        }
                    });
                }
            },
            deleteSubscription(id) {
                var confi = confirm("取消订阅该城市?");
                if (confi == true) {
                    this.axios({
                        url: '/mobile/user/deleteSubscription.action',
                        method: 'get',
                        params: {
                            id
                        }
                    }).then((response) => {
                        if (response.data == 1) { //移除成功
                            this.getSubscription();
                            this.$emit("showAlert", true, "移除成功!");
                        } else if (response.data == -1) { //移除失败,登录信息过期
                            this.$emit("showAlert", true, "移除失败!登录信息已过期");
                        } else if (response.data == 0) { //移除失败,服务器异常
                            this.$emit("showAlert", true, "移除失败!服务器无响应");
                        }
                    }).catch((error) => {
                        console.log(error);
                    });
                }
            }
        },
        mounted() {
            this.getUser();
            this.getSubscription();
        },
        filters: {
            formatDate(value) {
                if (value == "请先登录") {
                    return null;
                }
                let date = new Date(value);
                let y = date.getFullYear();
                let MM = date.getMonth() + 1;
                MM = MM < 10 ? ('0' + MM) : MM;
                let d = date.getDate();
                d = d < 10 ? ('0' + d) : d;
                let h = date.getHours();
                h = h < 10 ? ('0' + h) : h;
                let m = date.getMinutes();
                m = m < 10 ? ('0' + m) : m;
                let s = date.getSeconds();
                s = s < 10 ? ('0' + s) : s;
                return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
            }
        },
    };
</script>

<style scoped>

</style>