<template>
    <footer>
        <nav class="navbar navbar-expand-lg navbar-light bg-light">
            <a class="navbar-brand" href="/#/home">Aweather主页</a>
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav"
                aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarNav">
                <ul class="navbar-nav">
                    <li class="nav-item" :class="{active: '/weather'===$route.path}">
                        <a class="nav-link" href="/#/weather">天气预报 <span class="sr-only">(current)</span></a>
                    </li>
                    <li class="nav-item" :class="{active: '/user'===$route.path}">
                        <a class="nav-link" href="/#/user">用户中心</a>
                    </li>
                    <li class="nav-item" :class="{active: '/about'===$route.path}">
                        <a class="nav-link" href="/#/about">关于我</a>
                    </li>
                </ul>
            </div>
            <div class="col-md-2">
                <button class="btn btn-success glyphicon glyphicon-map-marker" @click="$emit('getCity')"> 定位
                </button>
            </div>
            <div class="col-md-2">
                <select class="form-control" v-model="selectProvince" @change="getCityByProvince">
                    <option selected>-选择省份-</option>
                    <option v-for="province in provinceArrray" :key="province.id">{{province}}</option>
                </select>
            </div>
            <div class="col-md-2">
                <select class="form-control" v-model="selectCity" @change="getLocation">
                    <option selected>-选择地区-</option>
                    <option v-for="city in cityArray" :key="city.id">{{city}}</option>
                </select>
            </div>
            <div class="col-md-1" align="right">
                <div class="btn-group" role="group" aria-label="...">
                    <button v-if="!ifLogin" type="button" class="btn btn-info" data-toggle="modal"
                        data-target="#loginAndRegisterModal">
                        登录
                    </button>
                    <button v-if="ifLogin" type="button" class="btn btn-danger" @click="logout">
                        注销
                    </button>
                </div>
            </div>
        </nav>
        <div class="div_middle" style="margin-top:5px;">
            <h5>
                <span style="color:black;font-size:15px;">Copyright ©2019 WuZhenghua</span>
            </h5>
        </div>

    </footer>
</template>

<script>
    export default {
        name: 'footer',
        data() {
            return {
                ifLogin: false,
                selectProvince: '-选择省份-',
                selectCity: '-选择地区-',
                provinceArrray: [],
                cityArray: []
            }
        },
        methods: {
            logout() { //注销服务
                var confi = confirm("是否注销当前用户?");
                if (confi == true) {
                    this.axios({
                        url: "/mobile/user/logout.action",
                        method: "get"
                    }).then((response) => {
                        if (response.data == 1) {
                            this.$emit("clearUser");
                            this.ifLogin = false;
                            this.$emit("showAlert", "true", "注销成功!");
                        } else {
                            alert("服务器错误!");
                        }
                    }).catch((error) => {
                        alert("服务器错误!");
                        console.log(error);
                    });
                }
            },
            getAllProvince() { //获取所有省份
                this.axios({
                    method: "get",
                    url: "/mobile/city/getAllProvince.action"
                }).then((response) => {
                    this.provinceArrray = response.data;
                }).catch((error) => {
                    console.log(error);
                });
            },
            getCityByProvince() { //根据省份名称获取所有地区名称
                this.selectCity = "-选择地区-";
                this.axios({
                    method: "get",
                    params: {
                        province: this.selectProvince
                    },
                    url: "/mobile/city/getCityByProvince.action"
                }).then((response) => {
                    this.cityArray = response.data
                }).catch((error) => {
                    console.log(error);
                });
            },
            getLocation() { //显示顶部定位数据,随着所选地区改变
                if (this.selectCity != '-选择地区-') {
                    this.$emit("getLocation", this.selectProvince, this.selectCity);
                }
            },
        },
        computed: {

        },
        mounted() {
            this.getAllProvince();
            this.getCityByProvince();
        }
    }
</script>

<style scoped>
    footer {
        height: 50px;
        width: 100%;
        opacity: 0.9;
        border-radius: 5px;
    }
</style>