/*
路由器对象模块
*/
import Vue from 'vue';
import VueRouter from 'vue-router';

import Home from '../../views/pc/Home.vue';
import User from '../../views/pc/User.vue';
import Weather from '../../views/pc/Weather.vue';
import Pay from '../../views/pc/Pay.vue';
import About from '../../views/pc/About.vue';
// import Frog from '../views/Frog.vue';

//声明使用插件
Vue.use(VueRouter);


let router =  new VueRouter({
    routes: [{
            path: '/home',
            component: Home
        },
        {
            path: '/user',
            component: User
        },
        {
            path: '/weather',
            component: Weather
        }, {
            path: '/pay',
            component: Pay
        }, {
            path: '/about',
            component: About
        },
        {
            path: '/',
            redirect: '/home'
        }
    ]
});

export default router
