<template>
    <main id="weather">
        <img v-if="loading" src="../../assets/images/common/loading.gif"
            style="position:absolute;z-index:9999;height:681px;width:1110px;" oncontextmenu="return false"
            onselectstart="return false" ondragstart="return false">

        <button class="alert alert-info col glyphicon glyphicon-hand-right" @click="getWeatherByCityId">
            点我获取最新信息</button>
        <div class="alert alert-primary" role="alert">
            <div class="row" v-for="weather in weatherArray.slice(0,1)" :key="weather.id">
                <div class="col-md-2">
                    <img :src="wea_img" oncontextmenu="return false" onselectstart="return false"
                        ondragstart="return false" style="width:80%;">
                </div>
                <div class="col-md-2 alert alert-warning">
                    <span style="font-size:40px">地区</span><br>
                    <span style="font-size:22px">{{cityZh}}</span>
                </div>
                <div class="col-md-3">
                    <div class="alert alert-warning" role="alert">
                        <span style="font-size:18px">
                            <b>天气:</b> {{weather.wea}}
                        </span><br />
                        <span style="font-size:18px">
                            <b>温度:</b> {{weather.tem}}
                        </span><br />
                        <span style="font-size:18px">
                            <b>风速:</b> {{weather.win_speed}}
                        </span><br />
                        <span style="font-size:18px">
                            <b>湿度:</b> {{weather.humidity}}
                        </span>
                    </div>
                </div>
                <div class="col-md-3">
                    <div class="alert alert-warning" role="alert">
                        <span style="font-size:18px">
                            <b>白天温度:</b> {{weather.tem1}}
                        </span><br />
                        <span style="font-size:18px">
                            <b>夜晚温度:</b> {{weather.tem2}}
                        </span><br />
                        <span style="font-size:18px">
                            <b>空气质量:</b> {{weather.air_level}}
                        </span><br />
                        <span style="font-size:18px">
                            <b>空气指数:</b> {{weather.air}}
                        </span>
                    </div>
                </div>
                <div class="col-md-2">
                    <div class="alert alert-danger" role="alert">
                        <span style="font-size:18px">
                            <b>预警信息:</b> 无
                        </span><br />
                    </div>
                </div>
            </div>

            <div class="alert alert-success" role="alert">
                <div class="row">
                    <div class="col" v-for="hour in hoursArray" :key="hour.id">
                        <span style="font-size:13px">
                            {{hour.day}}
                        </span><br />
                        <span style="font-size:18px">
                            {{hour.tem}}
                        </span><br />
                        <span style="font-size:18px">
                            {{hour.wea}}
                        </span><br />
                    </div>
                    <div class="col-md-1"></div>
                </div>
            </div>
        </div>
        <div class="alert alert-info " v-for="weather in weatherArray.slice(0,1)" :key="weather.id">
            <font size="3" class="glyphicon glyphicon-tags">
                Tip: {{weather.air_tips}}
            </font>
        </div>
        <div class="alert alert-primary" role="alert" style="height:36%">
            <h1>未来六日天气趋势 <span class="glyphicon glyphicon-stats"></span></h1><br>
            <div class="row">
                <div class="col-md-2" v-for="(weather) in weatherArray.slice(1, 7)" :key="weather.id">
                    <img :src="getImgUrl(weather.wea_img)" oncontextmenu="return false" onselectstart="return false"
                        ondragstart="return false" /><br />
                    <h5>{{weather.day}}</h5>
                    <h5>{{weather.wea}}</h5>
                </div>
            </div>
        </div>
    </main>
</template>

<script>
    export default {
        name: 'weather',
        data() {
            return {
                weather: {
                    day: "", //天,4日(今天)
                    date: "", //年月日,2019-08-04
                    week: "", //周几,星期日
                    wea: "", //天气,雷阵雨
                    wea_img: "", //图片名称,lei
                    air: "", //空气指数,55
                    humidity: "", //湿度,56
                    air_level: "", //空气等级,良
                    air_tips: "", //小贴士
                    tem1: "", //白天温度
                    tem2: "", //晚上温度
                    tem: "", //当前温度
                    win_speed: "" //风速,<3级
                },
                hoursArray: [],
                weatherArray: [],
                cityZh: '',
                wea_img: "",
                loading: false //加载gif
            }
        },
        methods: {
            getWeatherByCityId() {
                this.loading = true;
                this.axios({
                    method: "get",
                    url: "/mobile/weather/getWeather.action"
                }).then((response) => {
                    var weatherJson = response.data[0];
                    var hoursJson = response.data[1];
                    var cityJson = response.data[2];

                    this.weatherArray = JSON.parse(weatherJson);
                    this.hoursArray = JSON.parse(hoursJson);
                    this.cityZh = cityJson;
                    this.wea_img = require("../../assets/images/weather/" + this.weatherArray[0].wea_img + ".png");
                    this.$router.push('/weather');

                    this.loading = false;
                }).catch((error) => {
                    console.log(error);
                });


            },
            getImgUrl(wea_img) {
                return require("../../assets/images/weather/" + wea_img + ".png");
            },
        },
        created() {
            this.getWeatherByCityId();
        }
    }
</script>

<style scoped>

</style>