<template>
  <div id="app">
    <div class="container">
      <br>
      <v-header :userName="userName" :location="location"></v-header><br>
      <transition mode="out-in">
        <router-view ref="routerView" v-on:showAlert="showAlert" v-on:changeUserName="changeUserName"
          v-on:getLocation="getLocation" v-on:getUser="getUser"></router-view>
      </transition>

      <br>
      <v-footer ref="footer" v-on:clearUser="clearUser" v-on:getLocation="getLocation" v-on:getCity="getCity"
        v-on:showAlert="showAlert">
      </v-footer>

    </div>

    <!-- sliderCheckDiv-注册时的滑块验证码 -->
    <transition>
      <div id="sliderCheckDiv" v-show="showCheck">
        <h5 id="sliderTitle">请证明你不是机器人</h5>
        <slider-check v-on:doRegister="doRegister"></slider-check>
        <button class="btn btn-danger" @click="showCheck = false;clearUser();" style="margin:2%">取消注册</button>
      </div>
    </transition>

    <!-- loginAndRegisterModal -->
    <div id="loginAndRegisterModal" class="modal fade" tabindex="-1" role="dialog"
      aria-labelledby="loginAndRegisterTitle" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div id="login" class="modal-content">
          <transition mode="out-in">
            <component :is="loginOrRegister" :data="data" :user="user"></component>
          </transition>
          <div class="modal-footer">
            <span id="loginOrRegisterSpan"></span>
            <a href="#"><img src="../../assets/images/common/QQ.jpg" style="height:36px;"></a>
            <button type="button" class="btn btn-info" @click="doLogin();">登录</button>
            <button type="button" class="btn btn-secondary" @click.prevent="registerToCheck();">注册新用户</button>
          </div>
        </div>
      </div>
    </div>

    <!-- Alert -->
    <div id="alertModal" class="modal fade" tabindex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle"
      aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">通知</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            {{alertMsg}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import "bootstrap/dist/css/bootstrap.css";

  import vHeader from '../../components/pc/VHeader.vue';
  import vFooter from '../../components/pc/VFooter.vue';

  import login from '../../components/pc/Login.vue';
  import register from '../../components/pc/Register.vue';

  import sliderCheck from '../../components/pc/SliderCheck.vue';

  export default {
    data() {
      return {
        user: {
          name: '',
          email: '',
          password: '',
          created: ''
        },
        data: {
          confirmPassword: ''
        },
        userName: '',
        alertMsg: '通知',
        confirmAlertMsg: '通知',
        location: '定位中...',
        loginOrRegister: 'login',
        showCheck: false
      }
    },
    components: {
      vHeader,
      vFooter,
      login,
      register,
      sliderCheck
    },
    methods: {
      doLogin() {
        if (this.loginOrRegister == 'login') {
          this.loginOrRegisterSpan(true, "登陆中...");
          this.axios({
            method: 'post',
            url: "/mobile/user/doLogin.action",
            data: this.user
          }).then((response) => {
            var msg = "登录成功";
            if (response.data == 1) {
              this.getUser();
              $("#loginAndRegisterModal").modal('hide');
              $("#loginOrRegisterSpan").html("");
              this.$refs.routerView.getUser();
              this.clearUser();
              this.showAlert(true, "欢迎您!" + this.user.name);
            }
            if (response.data == 2) {
              msg = "密码错误";
            }
            if (response.data == 3) {
              msg = "邮箱不存在";
            }
            if (response.data == 4) {
              msg = "邮箱格式有误";
            }
            if (response.data == 5) {
              msg = "该邮箱已被注册";
            }
            if (response.data == 6) {
              msg = "遇到未知错误,请稍后重试";
            }
            if (response.data != 1) {
              $("#loginOrRegisterSpan").html("<span class='glyphicon glyphicon-remove' style='color: red'>" + msg +
                "</span>");
            }
          }).catch((error) => {
            console.log(error);
          });
        } else {
          this.loginOrRegister = 'login';
          this.clearUser();
        }
      },
      registerToCheck() { //注册验证服务
        if (this.loginOrRegister == 'register') {
          var email = this.user.email;
          var name = this.user.name;
          var password = this.user.password;
          var confirmPassword = this.data.confirmPassword;
          var msg = "";
          if (!(/^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/).test(email)) {
            msg = "请正确填写邮箱";
            this.loginOrRegisterSpan(false, msg);
          } else if (password == "") {
            msg = "请填写密码";
            this.loginOrRegisterSpan(false, msg);
          } else if (password != confirmPassword) {
            msg = "两次输入的密码不同";
            this.loginOrRegisterSpan(false, msg);
          } else {
            this.showCheck = true;
            $("#loginAndRegisterModal").modal('hide');
            // this.doRegister();
          }
        } else {
          this.loginOrRegister = 'register';
          this.clearUser();
        }
      },
      doRegister() { //注册服务
        var msg = "";
        this.axios({
          url: '/mobile/user/doRegist.action',
          method: 'post',
          data: this.user
        }).then((response) => {
          if (response.data == 2) {
            msg = "邮箱不能为空";
          }
          if (response.data == 3) {
            msg = "密码不能为空";
          }
          if (response.data == 4) {
            msg = "邮箱格式有误";
          }
          if (response.data == 5) {
            msg = "该邮箱已被注册";
          }
          if (response.data == 6) {
            msg = "遇到未知错误,请稍后重试";
          }
          if (response.data == 1) { //注册成功
            msg = "注册成功";
            this.clearUser();
            this.loginOrRegisterSpan(true, msg);
            $("#loginAndRegisterModal").modal('show');
            this.loginOrRegister = "login";
          } else { //注册失败
            this.loginOrRegisterSpan(false, msg);
            this.clearUser();
            this.showAlert(true, "抱歉!注册失败," + msg);
          }
        }).catch((error) => {
          console.log(error);
        });
        this.showCheck = false; //关闭滑动验证码
      },
      getUser() {
        this.axios({
          url: '/mobile/user/getUserMessage.action',
          method: 'get'
        }).then((response) => {
          if (response.data != '' && response.data != null) {
            this.user = response.data;
            this.userName = this.user.name;
            this.$refs.footer.ifLogin = true;
          }
        }).catch((error) => {
          console.log(error);
        });
      },
      changeUserName(userName) {
        this.userName = userName;
      },
      showAlert(ifShow, alertMsg) {
        if (ifShow) {
          $('#alertModal').modal('show');
          this.alertMsg = alertMsg;
        } else {
          $('#alertModal').modal('hide');
          this.alertMsg = alertMsg;
        }
      },
      loginOrRegisterSpan(flag, spanMsg) {
        if (flag) {
          $("#loginOrRegisterSpan").html("<span class='glyphicon glyphicon-ok' style='color: green'>" + spanMsg +
            "</span>");
        } else {
          $("#loginOrRegisterSpan").html("<span class='glyphicon glyphicon-remove' style='color: red'>" + spanMsg +
            "</span>");
        }
        if (spanMsg == "" || flag == null) {
          $("#loginOrRegisterSpan").html("");
        }
      },
      clearUser() { //清空当前用户信息
        this.user.email = '';
        this.user.password = '';
        this.user.name = '';
        this.user.created = '';
        this.userName = '';
        this.data.confirmPassword = '';
        this.loginOrRegisterSpan(null, "");
        if (this.$route.path == "/user") {
          this.$refs.routerView.getSubscription();
          this.$refs.routerView.getUser();
        }
      },
      getLocation(selectProvince, selectCity) { //显示顶部定位数据,随着所选地区改变
        this.location = selectCity;
        this.setCityIdToCookie(selectProvince, this.location);
      },
      setCityIdToCookie(province, city) {
        this.axios({
          method: "get",
          url: "/mobile/city/setCityIdToCookie.action",
          params: {
            provinceZh: province,
            cityZh: city
          }
        }).then((response) => {
          if (this.$route.path == "/weather") {
            this.$refs.routerView.getWeatherByCityId();
          }
        }).catch((error) => {
          console.log(error);
        });
      },
      removeCityIdFromCookie() {
        this.axios({
          method: "get",
          url: "/mobile/city/removeCityIdFromCookie.action"
        }).catch((error) => {
          console.log(error);
        })
      },
      getCity() { //定位地区
        this.removeCityIdFromCookie(); //定位不需要取cookie里的城市id,所以先清除
        this.$refs.footer.selectCity = '-选择地区-';
        this.$refs.footer.selectProvince = '-选择省份-';
        this.location = '定位中...';
        const geolocation = new BMap.Geolocation();
        geolocation.getCurrentPosition((position) => {
          let city = position.address.city; //获取城市信息
          let province = position.address.province; //获取省份信息
          city = city.replace("市", "");
          city = city.replace("区", "");
          city = city.replace("县", "");
          province = province.replace("省", "");
          province = province.replace("市", "");
          province = province.replace("特别行政区", "");

          this.setCityIdToCookie(province, city);
          this.location = city;
          // $("#locationLoading").css("display","none");
        }, (error) => {
          // $("#locationLoading").css("display","none");
          this.location = "定位失败";
          alert("请确定是否开启了定位,或者手动选择位置");
        }, {
          provider: 'baidu'
        });
      }
    },
    mounted() {
      this.getUser();
      this.getCity();
    }
  }
</script>


<style>
  body {
    background-image: url('../../assets/images/common/background_skyAndCloud.png');
    height: 100%;
    width: 100%;
    background-size: cover;
    /* background-color: rgb(133, 216, 255); */
  }

  .div_middle {
    text-align: center;
  }

  /* v-enter [这是一个时间点] 进入之前,元素初始状态,此时还没有开始进入 */
  /* v-leave-to [这是一个时间点] 是动画离开之后,离开的终止状态,此时动画已经结束 */
  .v-enter,
  .v-leave-to {
    opacity: 0;
    transform: translateX(2000px);
  }

  /* v-enter-active [入场动画的时间段] */
  /* v-leave-active [离场动画的时间段] */
  .v-enter-active,
  .v-leave-active {
    transition: all 0.4s ease;
  }

  .big {
    width: 100%;
    height: 100%;
  }

  main {
    height: 680px;
    width: 100%;
    background-color: white;
    opacity: 0.9;
    border-radius: 5px;
  }

  #sliderCheckDiv {
    padding-top: 4%;
    position: fixed;
    *position: relative;
    top: 37%;
    left: 39.5%;
    height: 250px;
    width: 400px;
    background-color: white;
    opacity: 0.9;
    display: block;
    text-align: center;
    border-radius: 15px;
  }
</style>